


















































































.user_address_list {
	height: 200px;
	width: 400px;
	box-shadow: inset 0 -3px 3px rgba(226, 223, 192, 0.5);
	
	&-item {
		margin: 0 0 20px 0;
		width: auto;
		text-decoration: none;
		padding-left: 5px;
	}
	
	.address_item_txt {
		line-height: 19px;
		font-size: 13px;
	}
	.b-button {
		width: 100%;
		display: flex;
	}
	
	.arrow_left_address {
		background: url(/local/dist/images/left_arr.png) 0 2px no-repeat;
		width: 19px;
		height: 25px;
		margin-right: 5px;
		cursor: pointer;
	}
	
	.edit__icon {
		background: url(/local/dist/images/sprite3.png) -595px -618px no-repeat;
		width: 19px;
		height: 19px;
		margin-right: 3px;
	}
}

.user_address_list__title {
	margin: 10px 0;
	font-size: 18px;
}

.user_address_wrap {
	.b-button_green {
		margin-top: 15px;
	}
}

.b-popup-hello-form__note {
	margin-bottom: 10px;
}

.wrap_address {
	.b-popup-hello-form__item {
		margin-top: 10px;
		margin-bottom: 15px;
	}
}




