


























































































































































































































































































































































.modal_content.wrap_address {
	top: 0;
	display: flex;
	justify-content: center;
	position: fixed;
	left: 2%;
}

.wrap_address {
	
	.b-popup-hello__wrapper {
		display: flex;
		height: 470px;
		padding: 0;
	}
	
	.b-popup-hello__right {
		text-align: left;
	}
	
	.b-popup-hello {
		position: relative;
		top: 20%;
	}
	
	.b-popup-hello__ymap,
	#map_hello {
		height: 470px !important;
	}
	
	.b-popup-hello__left {
		padding: 0;
	}
	
	.el-input__inner {
		font-size: 14px;
		-webkit-border-radius: 20px;
		border-radius: 20px;
		background-color: #fff;
		border: 1px solid #dedede;
		padding: 8px 10px;
		width: 100%;
		height: 33px;
	}
	
	.el-input-group__append {
		border: none;
		color: inherit;
		background-color: transparent;
	}
	.b-popup-hello-form__item .b-form-control {
		padding-right: 0;
	}
	
	.el-autocomplete {
		display: block;
	}
}

.address_edit {
	background: #fff;
	margin-top: 10%;
	
	.lk__add-address {
		text-align: left !important;
		
		.b-button {
			margin-left: 97px;
			width: auto;
			padding: 0 25px;
		}
	}
	
	.form__label {
		.span__label {
			width: 90px;
		}
	}
	
	.form__input_middle {
		width: 440px;
	}
	
	.form__label {
		display: flex;
		align-items: center;
		
		.el-input__inner {
			background: none;
			border: none;
			font-size: 18px;
			color: #808080 !important;
		}
		
		.el-autocomplete {
			padding: 10px;
		}
	}
}

#ya_maps_hello_modal, .maps_hello_modal, #map_hello_modal {
	height: 314px;
}
.maps_hello_modal {
	margin-top: 13px;
}

.close_change_address {
	top: 16px;
}

.b-popup-hello__items.b-ib-wrapper {
	padding-top: 15px;
}




